// скрытие и показ submenu по hover в nav.less

.submenu {
  grid-column: 1/-1;
  padding: 10px 30px;
  background-color: var(--light);
  border-bottom: 1px solid var(--light-gray);

  @media (min-width: @desktop-width) {
    display: grid;
    row-gap: 5px;
    position: absolute;
    top: 62px;
    left: 50%;
    width: 1200px;
    height: 430px;
    margin-top: 1px;
    padding: 40px 100px 40px 250px;
    visibility: hidden;
    opacity: 0;
    transition-delay: 0.6s;
    transform: translateX(-50%);
    z-index: 1;
    background-color: var(--white);
    border-radius: 5px;
    box-shadow: 0 5px 10px 0 var(--light-black);

    // background-color: rgba(0, 0, 0, 0.5);
    // box-shadow: 0 0 0 99999px rgba(0, 0, 0, 0.5); /* fallback for IE */
    // box-shadow: 0 0 100vmax 100vmax rgba(0, 0, 0, 0.5);
  }
}

.submenu--links, .submenu--expert {
  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, min-content);
    column-gap: 60px;
  }
}

.submenu__title {
  display: block;
  margin-top: 15px;
  padding: 5px 0;
  font-size: 16px;
  font-weight: 700;

  @media (min-width: @desktop-width) {
    position: relative;
    margin: 0;

    &:not(:first-child)::after {
      position: absolute;
      content: '';
      top: 50px;
      left: -31px;
      width: 2px;
      height: 200px;
      background-color: var(--extra-light-gray);
    }
  }
}

.submenu__list {
  @media (min-width: @desktop-width) {
    position: relative;
    grid-row: 2/3;
  }
}

.submenu__list--links li a {
  display: block;
  padding: 10px 0;
  font-size: 14px;

  @media (min-width: @desktop-width) {
    position: relative;
    padding: 8px 10px;
    font-size: inherit;

    & span {
      position: relative;

      &:hover::after {
        position: absolute;
        content: '';
        left: 0;
        bottom: -4px;
        width: 100%;
        height: 1px;
        background-color: var(--dark-blue);
      }
    }
  }
}

.submenu--bldg {
  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
}

.submenu__list--bldg {
  display: grid;
  row-gap: 20px;

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(3, min-content);
    column-gap: 20px;
  }
}

.submenu--bldg .submenu__title,
.submenu--expert .submenu__title {
  margin-bottom: 15px;
}

.submenu__list--expert {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.submenu__link-wrapper {
  grid-row: 3/4;
  margin-top: 10px;

  & a {
    display: inline-block;
    padding: 10px 0;
  }

  & a span {
    font-size: 15px;
    color: var(--blue);
    border-bottom: 1px dashed var(--blue);
  }
}
